import React, { useState, useEffect } from 'react';
import { Form, Button, Card, Image, Alert } from 'react-bootstrap';
import { useAuth } from '../context/AuthContext';
import { userService } from '../services/api';

const ProfilePage = () => {
  const { user } = useAuth();
  const [formData, setFormData] = useState({
    username: '',
    email: '',
    phoneNumber: '',
    altEmail: '',
    profilePicture: null
  });
  const [previewImage, setPreviewImage] = useState(null);
  const [error, setError] = useState('');
  const [success, setSuccess] = useState('');
  const [loading, setLoading] = useState(false);

  useEffect(() => {
    if (user) {
      setFormData({
        username: user.username || '',
        email: user.email || '',
        phoneNumber: user.phoneNumber || '',
        altEmail: user.altEmail || '',
        profilePicture: user.profilePicture || null
      });
      setPreviewImage(user.profilePicture);
    }
  }, [user]);

  const handleProfilePicChange = (e) => {
    if (e.target.files && e.target.files[0]) {
      const file = e.target.files[0];
      setPreviewImage(URL.createObjectURL(file));
      setFormData(prev => ({ ...prev, profilePicture: file }));
    }
  };

  const handleInputChange = (e) => {
    const { name, value } = e.target;
    setFormData(prev => ({ ...prev, [name]: value }));
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    setError('');
    setSuccess('');
    setLoading(true);

    try {
      const formDataToSend = new FormData();
      Object.keys(formData).forEach(key => {
        if (formData[key] !== null) {
          formDataToSend.append(key, formData[key]);
        }
      });

      await userService.updateUser(user.id, formDataToSend);
      setSuccess('Profile updated successfully!');
    } catch (error) {
      setError(error.message || 'Failed to update profile. Please try again.');
    } finally {
      setLoading(false);
    }
  };

  return (
    <div className="container py-4">
      <Card className="shadow-sm">
        <Card.Body>
          <h3 className="mb-4">Profile Settings</h3>
          
          {error && <Alert variant="danger">{error}</Alert>}
          {success && <Alert variant="success">{success}</Alert>}

          <Form onSubmit={handleSubmit}>
            <div className="d-flex flex-column align-items-center mb-4">
              <Image
                src={previewImage || 'data:image/jpeg;base64,'}
                roundedCircle
                width={120}
                height={120}
                alt="Profile"
                className="mb-2 shadow-sm border"
                style={{ objectFit: 'cover', background: '#f8f9fa' }}
              />
              <Form.Label className="btn btn-outline-secondary btn-sm mb-0 mt-2">
                Change Picture
                <Form.Control
                  type="file"
                  accept="image/*"
                  onChange={handleProfilePicChange}
                  style={{ display: 'none' }}
                />
              </Form.Label>
            </div>

            <Form.Group className="mb-3" controlId="formUsername">
              <Form.Label>Username</Form.Label>
              <Form.Control
                type="text"
                name="username"
                value={formData.username}
                onChange={handleInputChange}
                required
              />
            </Form.Group>

            <Form.Group className="mb-3" controlId="formEmail">
              <Form.Label>Email address</Form.Label>
              <Form.Control
                type="email"
                name="email"
                value={formData.email}
                onChange={handleInputChange}
                required
              />
            </Form.Group>

            <Form.Group className="mb-3" controlId="formPhone">
              <Form.Label>Phone Number</Form.Label>
              <Form.Control
                type="tel"
                name="phoneNumber"
                value={formData.phoneNumber}
                onChange={handleInputChange}
                placeholder="Optional"
              />
            </Form.Group>

            <Form.Group className="mb-3" controlId="formAltEmail">
              <Form.Label>Alternate Email</Form.Label>
              <Form.Control
                type="email"
                name="altEmail"
                value={formData.altEmail}
                onChange={handleInputChange}
                placeholder="Optional"
              />
            </Form.Group>

            <Button 
              variant="primary" 
              type="submit" 
              className="w-100"
              disabled={loading}
            >
              {loading ? 'Saving...' : 'Save Changes'}
            </Button>
          </Form>
        </Card.Body>
      </Card>
    </div>
  );
};

export default ProfilePage; 